import onemessage from './oneMessage';
import {mapState} from 'vuex';

var socketTask;
var socketOpen = false;

export default {
	components: {
		onemessage,
	},
	data() {
		return {
			/**
			 * ChatMessageCard example
			ChatMessageCard = {
				this.senderId = senderId;
				this.receiverId = receiverId;
				this.msg = msg;		// 显示
				this.msgId = msgId; // 前端不需要用到
				this.creatTime = creatTime; // 显示 flag
			},
			*/
			
			chatContent: [{
				msgId: '0001',
				flag: '1',
				msg: '第一条消息',
				createDate: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0002',
				flag: '1',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0003',
				flag: '1',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0004',
				flag: '2',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '1'
			}, {
				msgId: '0006',
				flag: '1',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0006',
				flag: '2',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '1'
			}, {
				msgId: '0001',
				flag: '1',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0002',
				flag: '1',
				msg: 'abab',
				createDate: '11:29',
				messageStatus: '0'
			}],

			socketMsgQueue: [], // 未发送的消息队列
			textMsg: '', // 输入框中的text
			windowHeight: '',
			
			userInfo: '',
			friendInfo: '',
		}
	},
	
	computed: {
		...mapState([
			'chatMessageCard',
			'flashChatPage',
		])
	},
	
	watch: {
		chatMessageCard(newVal, oldVal) { //监听数据变化，即可做相关操作
			console.log("newVal:");
			console.log(newVal);
			// 渲染到窗口
			this.chatContent.push(newVal);
			this.scrollToBottom();
		},
		
		flashChatPage(newVal, oldVal) { //监听数据变化，即可做相关操作
			// 重载聊天记录就可
			// console.log("重载聊天记录就可");
			this.getChatHistory();
			this.scrollToBottom();
		}
	},
	
	onLoad: function(opt) {
		// 获取界面传参
		this.friendInfo = JSON.parse(opt.friendInfo);
		
		uni.setNavigationBarTitle({
			title: this.friendInfo.nickname
		});
		
		// 获取我的信息
		var userInfo = this.getGlobalUserInfo();
		if (this.isNull(userInfo)) {
			console.log("No userInfo!!");
			return;
		}
		this.userInfo = userInfo;
		
		// 获取屏幕高度
		var that = this;
		uni.getSystemInfo({
		  success: function(res) {
			that.windowHeight = res.windowHeight;
		  }
		});
		
		// 获取与该用户的聊天历史记录
		this.getChatHistory();
		this.scrollToBottom();
	},
	
	methods: {
		
		sendText() {
			if(!this.textMsg){
				return;
			}
			this.mySocket.sendObj(this.netty.CHAT, this.friendInfo.id, this.textMsg, null);
			this.textMsg = '';//清空输入框
			
			// 渲染到窗口
			// var message = {
			// 	msgId: '',
			// 	flag: this.chat.ME,
			// 	msg: this.textMsg,
			// 	createDate: '11:29',
			// 	messageStatus: '1',
			// }
			// this.chatContent.push(message);
			
			// 直接重新加载聊天历史, 代替渲染到窗口
			// this.getChatHistory();
			// this.scrollToBottom();
			
		},
		
		getChatHistory(){
			var localChatHistory = this.chat.getUserChatHistory(this.userInfo.id, this.friendInfo.id);
			this.chatContent = localChatHistory;
			// console.log(this.chatContent);
		},
		
		scrollToBottom(){
			// 将页面滚动到底部，延时滚动,等待渲染
			// 页面高度乘以列表长度...绝对能滚到底
			this.$nextTick( function(){
				uni.pageScrollTo({
					scrollTop: this.windowHeight * this.chatContent.length,
					duration: 0
				});
			});
		}
	}
}
